import React from "react";

import { arachne } from "./arachne.js";
import { SelectMenu } from "./elements.js";

class Import extends React.Component{
    constructor(props){
        super(props);
        this.state = {tab: "res", workLst: [], editionLst: []};
    }
    render(){
        const styleBody = {
            boxShadow: "0px 2px 7px rgb(217, 217, 217)",
            padding: "40px 10% 20px 10%",
            display: "grid",
            gridTemplateColumns: "200px auto",
            gridTemplateRows: "1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr auto",
            gridGap: "15px"
        }
        let mainBody = <div style={styleBody}></div>;
        switch(this.state.tab){
            case "res":
                mainBody = <div style={styleBody}>
                    <div style={{gridArea: "1/1/1/2"}}>Werk:</div>
                    <div style={{gridArea: "1/2/1/3"}}><SelectMenu options={this.state.workLst} onChange={()=>{}} /></div>
                    <div style={{gridArea: "2/1/2/2"}}>Ressource:</div>
                    <div style={{gridArea: "2/2/2/3"}}><SelectMenu options={[[0, "Edition (relevant)"], [1, "Edition (veraltet)"], [2, "Handschrift"], [3, "Alter Druck (relevant)"], [4, "Alter Druck (veraltet)"], [5, "Sonstiges"]]} onChange={()=>{}} /></div>
                    <div style={{gridArea: "3/1/3/2"}}>Editor:</div>
                    <div style={{gridArea: "3/2/3/3"}}><input type="text" style={{width: "96%"}} /></div>
                    <div style={{gridArea: "4/1/4/2"}}>Jahr:</div>
                    <div style={{gridArea: "4/2/4/3"}}><input type="text" style={{width: "96%"}} /></div>
                    <div style={{gridArea: "5/1/5/2"}}>Band:</div>
                    <div style={{gridArea: "5/2/5/3"}}><input type="text" style={{width: "96%"}} /></div>
                    <div style={{gridArea: "6/1/6/2"}}>Bandinhalt:</div>
                    <div style={{gridArea: "6/2/6/3"}}><input type="text" style={{width: "96%"}} /></div>
                    <div style={{gridArea: "7/1/7/2"}}>Reihe:</div>
                    <div style={{gridArea: "7/2/7/3"}}><SelectMenu options={[[0, ""], [1, "Migne PL"], [2, "ASBen."], [3, "ASBoll."], [4, "AnalBoll."], [5, "Mon. Boica"], [6, "Ma. Schatzverzeichnisse"], [7, "Ma. Bibliothekskataloge"]]} onChange={()=>{}} /></div>
                    <div style={{gridArea: "9/1/9/2"}}>Dateipfad:</div>
                    <div style={{gridArea: "9/2/9/3"}}><input type="text" style={{width: "96%"}} /></div>
                    <div style={{gridArea: "10/1/10/2"}}>.png-Dateien:</div>
                    <div style={{gridArea: "10/2/10/3"}}><input type="file" /></div>
                    <div style={{gridArea: "12/2/12/3"}}><input type="button" value="hochladen" /></div>
                </div>;
                break;
            case "ocr":
                styleBody.gridTemplateRows = "1fr 1fr 1fr 1fr auto";
                mainBody = <div style={styleBody}>
                    <div>Ressource:</div>
                    <div><SelectMenu options={this.state.editionLst} onChange={async ()=>{
                        // check if path allready exists
                        const pathCheck = await arachne.scan.get({path: ""});
                    }} /></div>
                    <div>.txt-Dateien:</div>
                    <div><input type="file" /></div>
                    <div style={{gridArea:"4/2/4/3"}}><input type="button" value="hochladen" /></div>
                </div>;
                break;
            case "zettel":
                break;
        }
        const styleBox = {
            "padding": "20px 20% 0 20%"
        }
        const styleHeader = {
            display: "flex",
            justifyContent: "space-evenly",
            border: "1px solid white",
            padding: "10px 20px"

        }
        const styleTab = {
            padding: "5px 10px",
            cursor: "default"
        }
        const styleTabActive = {
            padding: "5px 10px",
            borderBottom: "1px solid red",
            cursor: "default"
        }
        return (<div style={styleBox}>
            <div style={styleHeader}>
                <div style={this.state.tab==="res"?styleTabActive:styleTab} onClick={()=>{this.setState({tab: "res"})}}>Ressource</div>
                <div style={this.state.tab==="ocr"?styleTabActive:styleTab} onClick={()=>{this.setState({tab: "ocr"})}}>ocr-Dateien</div>
                <div style={this.state.tab==="zettel"?styleTabActive:styleTab} onClick={()=>{this.setState({tab: "zettel"})}}>Zettel</div>
            </div>
            {mainBody}
        </div>);
    }
    componentDidMount(){
        const loadOptions = async () => {
            const works = await arachne.work.getAll({select: ["id", "ac_web"]});
            let newWorkLst = [];
            for(const work of works){
                newWorkLst.push([work.id, work.ac_web]);
            }
            this.setState({workLst: newWorkLst});
            const editions = await arachne.edition.getAll({select: ["id", "label"]});
            let newEditionLst = [];
            for(const edition of editions){
                newEditionLst.push([edition.id, edition.label]);
            }
            this.setState({editionLst: newEditionLst});

        }
        loadOptions();
    }
}
class Help extends React.Component{
    render(){
        return (
    <div style={{padding: "20px 40px"}}>
        <p>
        <a href="https://gitlab.lrz.de/haeberlin/dmlw/-/wikis/00-Start">Hilfe und Informationen</a> zu dMLW finden Sie auf unsererer <a href="https://gitlab.lrz.de/haeberlin/dmlw">GitLab-Seite</a>.</p>
        <p>Informationen zu der aktuellen Version von dMLW und den Veränderungen finden Sie <a href="https://gitlab.lrz.de/haeberlin/dmlw/-/blob/master/CHANGELOG.md">in unserem Changelog</a>.</p>
        <p>Informationen zum Wörterbuch-Projekt auf <a href="www.mlw.badw.de">www.mlw.badw.de</a></p> 
    </div>
        );
    }
}

export { Help, Import };